import Vue from 'vue';
import { mapState, mapMutations, mapActions } from 'vuex';
import AddBoard from '@/components/AddBoard.vue';
export default Vue.extend({
    components: {
        AddBoard,
    },
    data() {
        return {
            isAddBoard: false,
        };
    },
    computed: {
        ...mapState(['boards']),
    },
    methods: {
        ...mapMutations(['SET_THEME']),
        ...mapActions(['FETCH_BOARDS']),
        onAddBoard() {
            this.isAddBoard = true;
        },
    },
    created() {
        this.FETCH_BOARDS();
        this.SET_THEME();
    },
});
