import Vue from 'vue';
import { mapActions } from 'vuex';
import Modal from '@/components/Modal.vue';
export default Vue.extend({
    components: {
        Modal,
    },
    data() {
        return {
            inputTitle: '',
        };
    },
    methods: {
        ...mapActions(['CREATE_BOARD']),
        close() {
            this.$emit('@close');
        },
        onSubmit() {
            const inputTitle = this.inputTitle.trim();
            if (inputTitle === '')
                return;
            this.CREATE_BOARD({ title: inputTitle }).then((data) => this.$router.push(`/b/${data.item.id}`));
        },
    },
    mounted() {
        const inputTitle = this.$refs.inputTitle;
        inputTitle.focus();
    },
});
