import * as api from '@/apis/index';
import bus from '@/utils/bus';
export const actions = {
    // Auth
    async LOGIN({ commit }, data) {
        bus.$emit('spinner:start');
        const response = await api.auth.login(data);
        commit('LOGIN', response.accessToken);
        bus.$emit('spinner:stop');
        return response;
    },
    // Board
    async FETCH_BOARDS({ commit }) {
        bus.$emit('spinner:start');
        const response = await api.board.fetch();
        commit('SET_BOARDS', response.list);
        bus.$emit('spinner:stop');
        return response;
    },
    async FETCH_BOARD({ commit }, bid) {
        bus.$emit('spinner:start');
        const response = await api.board.fetch(bid);
        commit('SET_BOARD', response.item);
        bus.$emit('spinner:stop');
        return response;
    },
    async CREATE_BOARD({ dispatch }, data) {
        bus.$emit('spinner:start');
        const response = await api.board.create(data);
        await dispatch('FETCH_BOARDS');
        bus.$emit('spinner:stop');
        return response;
    },
    async UPDATE_BOARD({ state, dispatch }, { bid, data }) {
        bus.$emit('spinner:start');
        const response = await api.board.update({ bid, data });
        await dispatch('FETCH_BOARD', state.board.id);
        bus.$emit('spinner:stop');
        return response;
    },
    async DELETE_BOARD({ dispatch }, bid) {
        bus.$emit('spinner:start');
        const response = await api.board.delete(bid);
        await dispatch('FETCH_BOARDS');
        bus.$emit('spinner:stop');
        return response;
    },
    // List
    async CREATE_LIST({ state, dispatch }, data) {
        bus.$emit('spinner:start');
        const response = await api.list.create(data);
        await dispatch('FETCH_BOARD', state.board.id);
        bus.$emit('spinner:stop');
        return response;
    },
    async UPDATE_LIST({ state, dispatch }, { lid, data }) {
        bus.$emit('spinner:start');
        const response = await api.list.update({ lid, data });
        await dispatch('FETCH_BOARD', state.board.id);
        bus.$emit('spinner:stop');
        return response;
    },
    async DELETE_LIST({ state, dispatch }, lid) {
        bus.$emit('spinner:start');
        const response = await api.list.delete(lid);
        await dispatch('FETCH_BOARD', state.board.id);
        bus.$emit('spinner:stop');
        return response;
    },
    // Card
    async FETCH_CARD({ commit }, cid) {
        bus.$emit('spinner:start');
        const response = await api.card.fetch(cid);
        commit('SET_CARD', response.item);
        bus.$emit('spinner:stop');
        return response;
    },
    async CREATE_CARD({ state, dispatch }, data) {
        bus.$emit('spinner:start');
        const response = await api.card.create(data);
        await dispatch('FETCH_BOARD', state.board.id);
        bus.$emit('spinner:stop');
        return response;
    },
    async UPDATE_CARD({ state, dispatch }, { cid, data }) {
        bus.$emit('spinner:start');
        const respone = await api.card.update({ cid, data });
        await dispatch('FETCH_BOARD', state.board.id);
        bus.$emit('spinner:stop');
        return respone;
    },
    async DELETE_CARD({ state, dispatch }, cid) {
        bus.$emit('spinner:start');
        const response = await api.card.delete(cid);
        await dispatch('FETCH_BOARD', state.board.id);
        bus.$emit('spinner:stop');
        return response;
    },
};
